export default "/* .card h4{display: block; width: 100%;} */\n.card dl, .card dt, .card dd{ display:inline-block;}\n.card{display: inline-block; width: calc(100% / 3 - 16px); height: 400px; vertical-align: middle;vertical-align: -webkit-baseline-middle;}\n.card img{ display: block; width:30%; height:auto; float: right;}\n.btn{border:1px solid #666; font-size: 14px; font-weight: 600; color:#666; display: inline-block; position: absolute; bottom: 20px; left: 20px;}\n.btn:hover,\n.card:hover .btn{color:#fff; background-color: #666;}\n.icon{margin:10px 0; display: flex; align-items: end;}\n.icon .material-icons{margin:0 5px;}\n.card dt{position: relative;padding-left: 10px;font-weight: 600; margin-top: 5px;}\n.card dt::before{content: ''; width: 4px; height: 4px; background:#666; border-radius: 50%; position: absolute; top: 7px; left: 0;}\n.card dt.blank::before{display: none;}\n.card dt:first-child{margin-top: 0;}\n.card dd{padding-left: 10px;}\n/* Responsive Styles */\n@media screen and (max-width: 767px) {\n\n    .card-container > *:not(.circle-link) ,\n    .terminal {\n      width: calc(100% - 16px);\n    }\n\n\n  }\n@media screen and (max-width: 575px) {\n    .card-container > *:not(.circle-link) ,\n    .terminal {\n      width: calc(100% - 16px);\n    }\n  }\n\n/*# sourceMappingURL=data:application/json;base64, */"